.projects {
    background: #1A1A1A;
    width: 100%;
    //display:flex;

    .projectsTitle {
        // text-align: left;
        //padding-left: 5vh;

        padding-top: 12vh;


        h1 {
            font-family: 'Poppins', sans-serif;
            font-size: 35px;
            margin: 0;
            color: white;
            font-weight: 200;
            letter-spacing: 1vh;
        }

        p {
            color: white;
            font-size: 20px;
            color: lightgray;

        }
        @media (max-width: 1200px) and (min-width: 801px) {
            h3{
                font-size: 55px;
            }
            p{font-size:20px;}
          }
          @media (max-width: 800px) {
                padding-bottom: 30px;
        }
    }

    .areashell {
        display: flex;
        justify-content: center;
        align-items: center;
    }

    .area {
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: 5vh;
        padding-bottom: 5vh;

        justify-content: center;
        // width: 70%;
        width: 80%;

        .container {

            align-items: center;
            justify-content: center;
            flex-wrap: wrap;

        }


    }
}

.item {

    text-align: left;
    justify-content: space-between;
    display: flex;
    border-bottom: 2px solid #CEC5FC;
    margin: 10px 20px;
    padding: 2vh;
    padding-bottom: 5vh;
    transition: all 1s ease;

    .itemInfo {
        h3 {
            font-size: 85px;
            padding-left: 1vh;
            margin: 0;
            padding-bottom: 1vh;
            color: white;

            @media (max-width: 800px) {
                font-size: 16px;
                padding-bottom: 0;
            }

        }

        p {
            color: white;
            margin: 0;
            font-size: 3vh;
            font-style: italic
        }
        @media (max-width: 1200px) and (min-width: 801px) {
            h3{
                font-size: 55px;
            }
            p{font-size:20px;}
          }
          @media (max-width: 800px) {
            
            h3{
                font-size: 32px;
                padding-bottom: 20px;
            }
            p{font-size:18px;
            padding-bottom: 20px;}
        }

    }

    .projectImage {
        height: 300px;
        width: 400px;
        overflow: hidden; // ensure scaling doesn't enlarge image onscreen
        border-radius: 20px; // ensure it keeps rounded image shape
        margin-left: 15px;
        img {
            width: 100%;
            height: 300px;
            object-fit: cover;
            z-index: 1;
            transition: transform .2s;

            /* Animation */
        }

        img:hover {
            transform: scale(1.2);
            width: 100%;
        }
        @media (max-width: 1200px) and (min-width: 801px) {
           
                height: 250px;
            
          }
          @media (max-width: 800px) {
            
                margin: 0;
                width: 250px;
                height: 200px;
            
        }

    }

    a {


        text-decoration: none;
        font-family: 'Raleway', sans-serif;

        :hover {
            color: #FCC5F3
        }

    }
    @media (max-width: 800px) {
        flex-direction: column;

    }
}

.seemore {

    font-size: 30px;
    padding-bottom: 15vh;
    margin: 0;

    :hover {
        background: #C5F3FC;
        color: black;

        .seemorelink {
            color: black
        }
    }

    .seemorelink {
        text-decoration: none;
        color: white;
        border: solid 1px white;
        border-radius: 2vh;
        padding: 3vh;

    }
}